<template>
    <div id="app" class="p-app-index">
        <Header></Header>
        <Breadcrumb name="魔盒矩阵" slug="app" root="/app" :feedbackEnable="true">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 224.993 224.993" v-bind:class="'u-app-index'" v-bind:slot="'logo'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M201.539 91.023L48.002 3.26A24.772 24.772 0 0035.728 0a24.763 24.763 0 00-12.412 3.34c-7.6 4.409-12.321 12.606-12.321 21.393V200.26c0 8.786 4.721 16.983 12.319 21.392a24.748 24.748 0 0012.413 3.342c4.296 0 8.541-1.128 12.274-3.261l153.535-87.764c7.686-4.394 12.46-12.621 12.46-21.473s-4.772-17.079-12.457-21.473zm-66.533-20.752l-21.081 29.364L63.5 29.397l71.506 40.874zM34.76 209.908c-1.371-.133-2.716-.533-3.917-1.23a9.763 9.763 0 01-4.848-8.418V24.732a9.762 9.762 0 014.849-8.418 9.746 9.746 0 013.902-1.25l69.946 97.432-69.932 97.412zm28.74-14.312l50.425-70.239 21.081 29.364L63.5 195.596zm130.594-74.651l-45.996 26.292-24.941-34.741 24.941-34.741 45.996 26.292c3.024 1.729 4.903 4.966 4.903 8.449s-1.878 6.721-4.903 8.449z"/></svg>
        </Breadcrumb>
        <div class="m-app m-app-index" ref="appContent">
            <h1 class="m-app-index-title">魔盒矩阵</h1>
            <div class="m-app-index-block" v-for="(group, key) in appGroupMap" :key="key">
                <el-divider content-position="left"><i class="el-icon-box"></i> {{ group }}</el-divider>
                <el-row :gutter="20">
                    <el-col :span="6" v-for="(app, index) in appMap[key]" :key="index">
                        <div class="u-item">
                            <a :href="app.link" target="_blank">
                                <span class="u-pic">
                                    <i class="u-icon">
                                        <img :src="getNewIcon(app.key)" />
                                    </i>
                                </span>
                                <span class="u-features">
                                    <span class="u-name">{{ app.label }}</span>
                                    <span class="u-desc">{{ app.remark }}</span>
                                </span>
                            </a>
                        </div>
                    </el-col>
                </el-row>
            </div>
        </div>
    </div>
</template>

<script>
import { __imgPath, __cdn } from "@jx3box/jx3box-common/data/jx3box.json";
import { reportNow } from "@jx3box/jx3box-common/js/reporter";
import { getAppJson } from "@/service/oss";
export default {
    name: "App",
    data: function () {
        return {
            appMap: {},
            appGroupMap: {
                common: "通用",
                pve: "PVE",
                pvp: "PVP",
                pvx: "PVX",
                other: "其他",
                special: "特殊",
            },
        };
    },
    computed: {
        prefix() {
            const prefix = this.$store.state.client === "std" ? "www" : "origin";
            return prefix;
        },
    },
    methods: {
        getIcon(key) {
            // return `/logos/${key}.svg`;
            // return __cdn + "logo-light/" + key + ".svg";
            return __imgPath + "image/box/" + key + ".svg";
        },
        getNewIcon(key) {
            return __cdn + "logo-light/" + key + ".svg";
        },
        handleClick(e) {
            // 取出a标签
            let a = e.target;

            // 如果不是a标签,则向上查找，直到找到a标签
            while (a.tagName !== "A") {
                a = a.parentNode;
            }

            let href = a.getAttribute("href");

            reportNow({
                caller: "matrix_all",
                data: {
                    href: `${this.prefix}:${href}`,
                },
            });
        },
    },
    mounted: function () {
        getAppJson().then((res) => {
            this.appMap = res;
        });
        this.$nextTick(() => {
            this.$refs.appContent.addEventListener("click", this.handleClick);
        });
    },
    beforeDestroy() {
        this.$refs.appContent.removeEventListener("click", this.handleClick);
    },
    components: {},
};
</script>

<style lang="less">
@import "~@/assets/css/app/index.less";
</style>
